import React from "react";

function Experience({ experience: { year, position, degree, devposition, content } }) {
  return (
    <div className="entry">
      <div className="title">
        <span>{position}</span>
        <br />
        <span>{year}</span>
      </div>
      <div className="body">
        <h4 className="mt-0">{degree}</h4>
        <h4 className="mt-0">{devposition}</h4>
        {Array.isArray(content) ? (
          content.map((item, index) => (
            <div key={index} className="content-row">
              {item}
            </div>
          ))
        ) : (
          <p>No content available.</p>
        )}
      </div>
    </div>
  );
}

export default Experience;
